export default {
	data(){
		return{
			form:{
				account:"",
				password:""
			}
		}
	},
	methods:{
		login(){
			if(!this.form.account){
				uni.showToast({
					icon:"none",
					title:"请输入账号"
				})
			}else if(!this.form.password){
				uni.showToast({
					icon:"none",
					title:"请输入密码"
				})
			}else{
				this.$http.post("securityUser/login",this.form).then(res=>{
					if(res.code==100){
						uni.showToast({
							icon:"success",
							title:"登录成功"
						})
						let userInfo=res.info
						userInfo=JSON.stringify(userInfo)
						uni.setStorageSync("userInfo",userInfo)
						setTimeout(()=>{
							uni.switchTab({
								url:"../index/index"
							})
						},2000)
					}
				})
			}
		},
	}
}
